div.screenshot-container {
	position: relative;
	width: 276px;
	height: 388px;
}
div.screenshot-container img {
	position: absolute;
	border: 1px solid #000;
	box-shadow: 10px 10px 5px -7px rgba(0, 0, 0, 0.75);
}
div.screenshot-container img.one {
	z-index: 10;
}
div.screenshot-container img.second {
	z-index: 5;
	top: 68px;
	left: 28px;
}
